import { login, register } from "@/api/user";
// 仅在客户端加载
const Cookie = process.client?require('js-cookie'):undefined
export default {
  name: "LoginIndex",
    middleware: 'notAuthenticated',
  data() {
    return {
      user: {
        username: "",
        email: "",
        password: "",
      },
      errors: {
        // email:['aa','bb'],
        // password:['cc','dd']
      },
    };
  },
  computed: {
    isLogin() {
      return this.$route.name === "login";
    },
  },
  methods: {
    async onSubmit() {
      try {
        const { data } = this.isLogin
        // TODO:保存用户的登录状态
          ? await login({
              user: this.user,
            })
          : await register({
              user: this.user,
            });
        console.log(data);
        this.$store.commit('setUser',data.user)
        Cookie.set('user',data.user)
        this.$router.push("/");
      } catch (error) {
        console.log(error);
        this.errors = error.response.data.errors;
      }
    },
  },
};
